import React from 'react'

import Layout from '../../components/layouts/Layout'
import StandartPageLayout from '../../components/layouts/StandartPageLayout'
import SEO from '../../components/Seo'

import Table from '../../components/common/Table'
import MainWidget from '../../components/common/Widgets/MainWidget'

import NumberCell from '../../components/ui/NumberCell'

import helpers from '../../helpers'

const columns = [
    {
        Header: 'Идентификационный номер',
        accessor: 'OrderNum', // String-based value accessors!
        minResizeWidth: 10,
    },
    {
        Header: 'Код класса инструментов',
        accessor: 'ClassCode', // String-based value accessors!
        minResizeWidth: 10,
    },
    {
        Header: 'Код инструмента',
        accessor: 'SecCode', // String-based value accessors!
        minResizeWidth: 10,
    },
    {
        Header: 'Код биржи',
        accessor: 'ExchangeCode', // String-based value accessors!
        minResizeWidth: 10,
    },
    {
        Header: 'Код клиента',
        accessor: 'ClientCode', // String-based value accessors!
        minResizeWidth: 10,
    },
    {
        Header: 'Цена за одну ценную бумагу',
        accessor: 'Price', // String-based value accessors!
        minResizeWidth: 10,
        Cell: NumberCell,
    },
    {
        Header: 'Количество ценных бумаг в лотах',
        accessor: 'Qty', // String-based value accessors!
        minResizeWidth: 10,
        Cell: NumberCell,
    },
    {
        Header: 'Количество ценных бумаг в штуках',
        accessor: 'Qty_pcs', // String-based value accessors!
        minResizeWidth: 10,
        Cell: NumberCell,
    },
    {
        Header: 'Торговый счет',
        accessor: 'Account', // String-based value accessors!
        minResizeWidth: 10,
    },
    {
        Header: 'Комментарий',
        accessor: 'BrokerRef', // String-based value accessors!
        minResizeWidth: 10,
    },
    {
        Header: 'Тип операции',
        accessor: 'Operation', // String-based value accessors!
        minResizeWidth: 10,
    },
    {
        Header: 'Состояние заявки',
        accessor: 'State', // String-based value accessors!
        minResizeWidth: 10,
    },
]

const tableProps = {
    columns,
    tableUrl: helpers.api.GET_ORDERS,
    tableName: 'Orders',
    defaultPageSize: 20,
}

const BriefcasePage = ({ location }) => (
    <Layout location={location}>
        <SEO title="Торговля" keywords={['gatsby', 'application', 'react']} />
        <StandartPageLayout location={location}>
            <Table {...tableProps} />
            <MainWidget location={location} mobile />
        </StandartPageLayout>
    </Layout>
)

export default BriefcasePage
